/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */
.wptravel-block-trip-outline{
    .col {
        display: flex;
        flex-wrap: wrap;
        .tc-heading {
            position: relative;
            width: calc(50% - 1px);
            padding: 0px 50px 15px 50px;
            text-align: right;
            h4 {
                font-size: 22px;
                text-transform: uppercase;
                margin-top: 0;
                margin-bottom: 0px;
            }
            h3:last-of-type {
                font-style: italic;
                font-weight: 100;
            }
        }
        h3 {
            margin-top: 0;
            font-size: 15px;
            font-weight: bold;
            margin-bottom: 3px;
            flex-basis: 100%;
        }
    }
    .timeline-contents .col:last-of-type .tc-content {
        padding-bottom: 0;
    }
    .tc-content.right {
        border-left: 2px solid;
    }
    .tc-content {
        position: relative;
        width: calc(50% - 1px);
        padding: 0 50px 15px 50px;
    }
    .itenary .timeline-contents .tc-content img {
        transform: translateY(8px);
    }
    .timeline-contents .col:last-of-type .tc-content p {
        margin-bottom: 0;
    }
    .itenary .image {
        height: 25px;
        width: 25px;
        left: -13px;
        background: #8b8b8b;
        top: 0;
        border-radius: 50%;
        border: 2px solid;
        position: absolute;
    }
}

.timeline-contents h2 {
    text-align: center;
}


#wptravel-block-trip-outline .second-design .wp-collapse-open a{
    border-radius: 30px;
    font-size: 10px;
 }

 #wptravel-block-trip-outline .second-design .panel-group{
    margin-left: 45px;
 }

 #wptravel-block-trip-outline .second-design .panel-heading [data-toggle=collapse].collapsed span.collapse-icon:after{
    content: "\f055";
 }

 #wptravel-block-trip-outline .second-design .panel-heading [data-toggle=collapse] span.collapse-icon:after{
    content: "\f056";
 }

 #wptravel-block-trip-outline .second-design h4:focus{
    outline: none;
 }

 #wptravel-block-trip-outline .second-design h4 a{
    font-size: 18px;
    border: none;
    padding: 10px 0px 20px 0px;
    outline: none;
 }

 #wptravel-block-trip-outline .second-design .panel-collapse{
    border: none;
 }

 #wptravel-block-trip-outline .second-design .panel-default{
    background-color: transparent;
    margin-bottom: 0px;
    border-bottom: 1px solid #ddd;
    position: relative;
 }

 #wptravel-block-trip-outline .second-design .panel-default:last-child{
    border-bottom: none;
 }

 #wptravel-block-trip-outline .second-design h4:hover{
    background-color: transparent;
 }

 #wptravel-block-trip-outline .second-design .panel-default:not(:last-child):after {
    content: "";
    position: absolute;
    z-index: 0;
    top: 40px;
    left: -45px;
    width: 1px;
    height: calc(100% + 10px);
    height: -webkit-calc(100% + 10px);
    height: -moz-calc(100% + 10px);
    background-image: linear-gradient(to bottom, rgba(28, 32, 17, .2) 40%, rgba(255, 255, 255, 0) 20%);
    background-position: left;
    background-size: 1px 5px;
    background-repeat: repeat-y;
}

#wptravel-block-trip-outline .second-design .panel-default:not(:last-child):before{
    position: absolute;
    content: "";
    width: 13px;
    height: 13px;
    background-color: var(--wp--preset--color--primary);
    left: -51px;
    top: 22px;
    transform: translateY(-50%);
    border-radius: 50%;
    z-index: 2;
}

#wptravel-block-trip-outline .second-design .panel-default:last-child:before {
    position: absolute;
    content: "\f024";
    font-family: "Font Awesome 5 Free";
    width: 35px;
    height: 35px;
    padding-top: 2px;
    text-align: center;
    background-color: var(--wp--preset--color--primary);
    color: #fff;
    left: -60px;
    top: 25px;
    transform: translateY(-50%);
    border-radius: 50%;
    z-index: 2;
}

#wptravel-block-trip-outline .second-design .panel-default:first-child:before {
    position: absolute;
    content: "\f3c5";
    font-weight: 900;
    font-family: "Font Awesome 5 Free";
    width: 35px;
    height: 35px;
    padding-top: 2px;
    text-align: center;
    background-color: var(--wp--preset--color--primary);
    color: #fff;
    left: -60px;
    top: 25px;
    transform: translateY(-50%);
    border-radius: 50%;
    z-index: 2;
}